* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #454547;
  margin-bottom: 16px;
  margin-left: 16px;
  margin-right: 16px;
  padding-top: 120px;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  line-height: 1.5;
}

img {
  width: 100%;
  height: auto;
}

input {
  font: inherit;
  padding: 8px;
}

button {
  cursor: pointer;
  padding: 8px 12px;
}

li {
  margin-bottom: 4px;
  list-style-type: none;
  display: flex;
}

ul {
  margin-left: 0;
  padding-left: 0;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.is-hidden {
  display: none;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(200px, auto);
  gap: 14px;
  display: grid;
  overflow: auto;
}

.photo-card {
  border-radius: 0;
  flex-direction: column;
  display: flex;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

.gallery__image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  max-width: 100%;
  display: block;
}

.info {
  text-align: center;
  background-color: rgba(247, 247, 247, .9);
  justify-content: space-between;
  align-items: center;
  padding: 10px 6px;
  display: flex;
}

.info-item {
  color: #333;
  margin: 0;
  font-size: 12px;
  line-height: 1.5;
}

.cards-link {
  flex-direction: column;
  text-decoration: none;
  display: flex;
}

.photo-card:hover .gallery__image {
  transition: transform .3s ease-in-out;
  transform: scale(1.1);
}

.box {
  color: rgba(122, 112, 112, .655);
  width: 100%;
  height: 120px;
  background-color: #454547;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: -120px;
  transition: all .5s;
  display: flex;
  position: fixed;
}

.input-div {
  position: relative;
}

.form-input {
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid rgba(255, 255, 255, .157);
  border-radius: 5px 0 0 5px;
  outline: none;
  padding: 10px;
  font-size: 1rem;
  transition: all .5s;
}

.form-input ~ span {
  text-transform: uppercase;
  letter-spacing: 1px;
  color: rgba(122, 112, 112, .655);
  z-index: -1;
  background-color: #454547;
  padding: 5px;
  transition: all .5s;
  position: absolute;
  top: 10px;
  left: 10px;
}

.form-input:valid ~ span, .form-input:focus ~ span {
  color: #fff;
  z-index: 1;
  border-left: 2px solid #fff;
  border-right: 2px solid #fff;
  padding: 0 10px;
  font-size: .8rem;
  top: 0;
  transform: translateY(-7px);
}

.form-input:focus, .form-input:valid {
  color: #fff;
  border: 2px solid #fff;
}

.search-form {
  min-width: 400px;
  display: flex;
}

.button {
  color: #e8e8e8;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #454547;
  border: 2px solid rgba(255, 255, 255, .157);
  border-radius: 0 5px 5px 0;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  transition: all .6s cubic-bezier(.23, 1, .32, 1);
  display: flex;
  position: relative;
  overflow: hidden;
}

.button .span {
  z-index: 2;
  transition: all .6s cubic-bezier(.23, 1, .32, 1);
  position: relative;
}

.button:before {
  content: "";
  width: 150%;
  height: 150%;
  border-radius: 15px;
  position: absolute;
  bottom: 0%;
  left: 0%;
  transform: translate(-100%, 100%)rotate(45deg);
}

.button:hover:before {
  animation: shakeBack .6s .6s forwards;
}

.button:hover .span {
  scale: 1.2;
}

.button:hover {
  box-shadow: none;
}

.button:active {
  scale: .95;
}

@keyframes shakeBack {
  0% {
    transform: translate(-100%, 100%)rotate(45deg);
  }

  50% {
    transform: translate(15%, -15%)rotate(45deg);
  }

  100% {
    transform: translate(-10%, 10%)rotate(45deg);
  }
}

